<template>
  <div class="dashboard">
    <div v-if="firstlineAssets !== null">
      <div
        class="assets__totalpaid"
      >      = {{totalPoolReward}}</div>
      <div class="table">
        <div class="row table__periods table__header--position">
          <div class="asset__periods assets__header">
            <div class="assetbox__adress--alias"></div>
            <div class="assetbox__adress--title"></div>
          </div>
          <div class="period__wrapper">
            <div class="asset__periods coll-reward" v-for="(period, index) in periods" :key="index">
              <div>{{ toDate(period)}}</div>
            </div>
          </div>
          <div class="asset__periods--fullperiod coll-reward">
            <div class="assetbox__title--fullperiod"> </div>
          </div>
        </div>

        <div class="row table__periods table__subheader--position">
          <div class="asset__periods assets__header">
            <div class="assetbox__adress--alias"></div>
            <div class="assetbox__adress--title"> (Bitbon.Today)</div>
          </div>
          <div class="period__wrapper">
            <div class="asset__amount" v-for="(period, index) in periods" :key="index">
              <div class="coll-reward">{{ rewardforPool(period) }}</div>
            </div>
          </div>

          <div class="asset__amount">
            <div class="coll-reward">{{ cleanReward }}</div>
          </div>
        </div>
        <div class="breakpoint"></div>
        <div class="row table__assets" v-for="(el,index) in firstlineAssets" :key="index">
          <div class="asset__adress assets__header">
            <div class="assetbox__adress--alias">{{el.alias}}</div>
            <div class="assetbox__adress--title">{{el.adress}}</div>
            <div class="asset__result">
              <div
                div
                class="asset__amount"
                v-for="(period, index) in periods"
                :key="index"
                :class="checkThePayment(el.adress,period)"
                @click="changeThePayment(el.adress, period)"
              >
                <div class="coll-reward">{{ rewardforPeriod(el.adress, period) }}</div>
              </div>
              <div div class="asset__amount">
                <div class="coll-reward">{{ rewardedPerAsset(el.adress) }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as firebase from "firebase/app";
require("firebase/database");

export default {
  data: function () {
    return {
      firebaseConfig: "",
      firstlineAssets: null,
      snapshot: null,
      periods: [],
      time: 0,
    };
  },
  methods: {
    toDate(el) {
      let startDate = new Date(el.start);
      let startMonth = startDate.getMonth();
      let start =
        startDate.getDate() +
        "." +
        (startMonth < 10 ? "0" : "") +
        startMonth +
        "." +
        startDate.getFullYear();

      let endDate = new Date(el.end);
      let endMonth = endDate.getMonth();
      let end =
        endDate.getDate() +
        "." +
        (endMonth < 10 ? "0" : "") +
        endMonth +
        "." +
        endDate.getFullYear();

      let result = `${start} - ${end}`;
      return result;
    },
    rewardforPeriod(asset, period) {
      let checked = this.snapshot.find((el) => el.adress == asset);
      if (this.time > period.end) {
        if (checked !== undefined) {
          if (
            checked.rewardedPeriod !== undefined &&
            checked.rewardedPeriod !== 0
          ) {
            for (
              let i = 0;
              i <= Object.keys(checked.rewardedPeriod).length - 1;
              i++
            ) {
              let checkedEl =
                checked.rewardedPeriod[Object.keys(checked.rewardedPeriod)[i]];

              if (
                checkedEl.start === period.start &&
                checkedEl.end === period.end
              ) {
                return `${checkedEl.rewardAmount}`;
              } else if (i == Object.keys(checked.rewardedPeriod).length - 1) {
                return 0;
              }
            }
          } else {
            return 0;
          }
        } else {
          return 0;
        }
      } else {
        return "-";
      }
    },
    rewardedPerAsset(el) {
      let tempCheckedEl = this.snapshot.find((element) => element.adress == el);
      if (tempCheckedEl !== undefined) {
        let result = 0;
        for (
          let i = 0;
          i < Object.keys(tempCheckedEl.rewardedPeriod).length;
          i++
        ) {
          result =
            result +
            tempCheckedEl.rewardedPeriod[
              Object.keys(tempCheckedEl.rewardedPeriod)[i]
            ].rewardAmount;
        }
        return result;
      }
    },
    rewardforPool(period) {
      let tempPeriodReward = period.poolReward;
      if (
        tempPeriodReward !== 0 &&
        tempPeriodReward !== undefined &&
        tempPeriodReward > 0
      ) {
        this.snapshot.forEach((el) => {
          for (let i = 0; i < Object.keys(el.rewardedPeriod).length; i++) {
            let tempRewPeriod =
              el.rewardedPeriod[Object.keys(el.rewardedPeriod)[i]];
            if (
              tempRewPeriod.start === period.start &&
              tempRewPeriod.end === period.end
            ) {
              tempPeriodReward = tempPeriodReward - tempRewPeriod.rewardAmount;
            }
          }
        });

        return tempPeriodReward;
      } else if (tempPeriodReward !== undefined && this.time >= period.end) {
        return tempPeriodReward;
      } else {
        return "-";
      }
    },
    changeThePayment(el, period) {
      let checked = this.snapshot.find((element) => element.adress == el);

      if (checked !== undefined) {
        for (let i = 0; i < Object.keys(checked.rewardedPeriod).length; i++) {
          let tempChangeEl =
            checked.rewardedPeriod[Object.keys(checked.rewardedPeriod)[i]];

          if (
            tempChangeEl.start === period.start &&
            tempChangeEl.end === period.end
          ) {
            tempChangeEl.paid = !tempChangeEl.paid;

            if (!firebase.apps.length) {
              this.firebaseConfig = this.$store.getters.getfirebaseConfig;
              firebase.initializeApp(this.firebaseConfig);
            }

            let database = firebase.database();
            let poolClients = database.ref("/firstline");

            poolClients.once("value").then((snapshot) => {
              let tempSnap = snapshot.val();
              let tempData = [];
              let keyAdress = null;

              if (tempSnap !== 0) {
                for (let i = 0; i < Object.keys(tempSnap).length; i++) {
                  if (tempSnap[Object.keys(tempSnap)[i]].adress === el) {
                    keyAdress = Object.keys(tempSnap)[i];
                    let checkedAssetforCommit =
                      tempSnap[Object.keys(tempSnap)[i]];
                    for (
                      let i = 0;
                      i <
                      Object.keys(checkedAssetforCommit.rewardedPeriod).length;
                      i++
                    ) {
                      let tempRewPer =
                        checkedAssetforCommit.rewardedPeriod[
                          Object.keys(checkedAssetforCommit.rewardedPeriod)[i]
                        ];
                      if (
                        tempRewPer.start === period.start &&
                        tempRewPer.end === period.end
                      ) {
                        let keyPeriod = Object.keys(
                          checkedAssetforCommit.rewardedPeriod
                        )[i];
                        if (
                          keyAdress !== null &&
                          keyAdress !== undefined &&
                          keyPeriod !== undefined
                        ) {
                          poolClients
                            .child(`/${keyAdress}`)
                            .child("/rewardedPeriod")
                            .child(`/${keyPeriod}`)
                            .update({ paid: tempChangeEl.paid });
                        }
                      }
                    }
                  }
                }
              }
            });
          }
        }
      }
    },
    checkThePayment(el, period) {
      let checked = this.snapshot.find((element) => element.adress == el);
      if (checked !== undefined) {
        for (let i = 0; i < Object.keys(checked.rewardedPeriod).length; i++) {
          let tempChangeEl =
            checked.rewardedPeriod[Object.keys(checked.rewardedPeriod)[i]];
          if (
            tempChangeEl.start === period.start &&
            tempChangeEl.end === period.end
          ) {
            if (tempChangeEl.paid === false) {
              return "period__nonpaid";
            } else if (tempChangeEl.paid === true) {
              return "period__paid";
            }
          }
        }
      }
    },
  },
  computed: {
    totalPoolReward() {
      let totalAm = 0;

      this.snapshot.forEach((el) => {
        if (el.totalPoolReward !== 0) {
          for (let i = 0; i < Object.keys(el.rewardedPeriod).length; i++) {
            totalAm =
              totalAm +
              el.rewardedPeriod[Object.keys(el.rewardedPeriod)[i]].rewardAmount;
          }
        }
      });

      return totalAm.toFixed(8);
    },
    cleanReward() {
      let result = 0;
      this.periods.forEach((el) => {
        el.poolReward !== undefined && el.poolReward >= 0
          ? (result = result + el.poolReward)
          : null;
      });
      return (result - this.totalPoolReward).toFixed(8);
    },
  },
  created() {
    if (!firebase.apps.length) {
      this.firebaseConfig = this.$store.getters.getfirebaseConfig;
      firebase.initializeApp(this.firebaseConfig);
    }

    let database = firebase.database();
    let poolClients = database.ref("/firstline");
    let poolPeriods = database.ref("/periods");

    poolClients.once("value").then((snapshot) => {
      let tempSnap = snapshot.val();
      let tempSnapAssetboxesData = [];
      let tempClientsList = [];

      if (tempSnap !== 0) {
        for (let i = 0; i < Object.keys(tempSnap).length; i++) {
          tempSnapAssetboxesData.push(tempSnap[Object.keys(tempSnap)[i]]);
        }
        tempSnapAssetboxesData.forEach((el) => {
          tempClientsList.push({ adress: el.adress, alias: el.alias });
        });
      }

      this.firstlineAssets = tempClientsList;
      this.snapshot = tempSnapAssetboxesData;

      poolPeriods.once("value").then((snap) => {
        let tempPeriodsforRendiring = [];
        let tempSnap = snap.val();

        if (tempSnap !== 0) {
          for (let i = 0; i < Object.keys(tempSnap).length; i++) {
            tempPeriodsforRendiring.push(tempSnap[Object.keys(tempSnap)[i]]);
          }
        }

        tempPeriodsforRendiring.forEach((element) => {
          let tempReward = 0;
          if (element.rewardData !== 0 && element.rewardData !== undefined) {
            element.rewardData.totalReward !== undefined
              ? (tempReward = element.rewardData.totalReward)
              : null;
          }

          this.periods.push({
            start: element.start,
            end: element.end,
            poolReward: tempReward,
          });
        });
      });

      this.time = new Date().getTime();

      /*
      tempData.forEach(el => {    
        if(tempClientsList.length > 0){
         let tempCheked = tempClientsList.find(element => element == el.adress);
  
         tempCheked == undefined ? newClient.push(el.adress) : null;
        } else {
          newClient.push(el.adress);
        }
      })*/
    });
  },
};
</script>

